.summary {
  min-height: 30vh;
  margin-bottom: 20px;
  &__date {
    display: inline-block;
    position: relative;
    font-size: 20px;
    font-weight: 600;
    color: var(--incomeColor);
    margin: 40px 0px 20px;
    &::before {
      content: "";
      position: absolute;
      bottom: -6px;
      left: 50%;
      height: 2px;
      width: 120px;
      background-color: var(--incomeColor);
      transform: translate(-50%, 0%);
      border-radius: 4px;
    }
  }
  &__expenses {
    display: inline-block;
    padding: 2px 12px;
    border: 1px solid var(--expenseColor);
    box-shadow: 0px 0px 2px 0px var(--expenseColor);
    border-radius: 4px;
    font-size: 18px;
    font-weight: 500;
  }
  .summary__sum {
    font-size: 36px;
    margin-top: 20px;
    border-radius: 4px;
  }
  .income-lger {
    color: var(--incomeColor);
    border: 1px solid var(--incomeColor);
    box-shadow: 0px 0px 2px 0px var(--incomeShadowColor1);
    &:hover {
      background-color: var(--incomeColor);
      color: #fff !important;
    }
  }
  .expense-lger {
    color: var(--expenseColor);
    border: 1px solid var(--expenseColor);
    box-shadow: 0px 0px 2px 0px var(--expenseShadowColor1);
    &:hover {
      background-color: var(--expenseColor);
      color: #fff !important;
    }
  }
}
